@import "colors";

#light {
  transition: all 0.3s ease 0s;
  background-color: $white;
  color: $black;

  .header {
    background-color: $white;
    border-bottom: 2px solid $black;

    .moon {
      cursor: pointer;

    }
  }

  .user {

  }

  .filter {
    color: $black;
    background-color: $white;

    &:hover {
      background-color: $black;
      color: $white;
    }
  }

  .filter-current {
    border: 2px solid $black;
    background-color: $black;
    color: $white;

    &:hover {
      background-color: $white;
      color: $black;
    }
  }

  .search-form {

    .search-input {
      background-color: $white;
      border: 2px solid $black;
      color: $black;
    }

    .search-button {
      background-color: $white;
      border: 2px solid $black;
      color: $black;

      &:disabled {
        cursor: initial;
        background-color: $white;
        border: 2px solid $black;
        color: $black;
      }

      &:enabled:hover {
        background-color: $black;
        border: 2px solid $white;
        color: $white;
      }
    }
  }

  .card {
    .badges {
      .badge {
        background-color: $white;
        color: $black;
        border: 2px solid $black;
      }
    }
  }

  .pagination {
    .active {
      border: 2px solid $white;
      background-color: $black;
      color: $white;
    }

    button {
      border: 2px solid $black;
      background-color: $white;
      color: $black;

      &:enabled:hover {
        border: 2px solid $white;
        background-color: $black;
        color: $white;
      }
    }
  }
}

#dark {
  transition: all 0.3s ease 0s;
  background-color: $black;
  color: $white;

  .header {
    border-bottom: 2px solid $white;
    background-color: $black;

    .sun {
      cursor: pointer;
    }
  }

  .user {
  }

  .filter {
    border: 2px solid $white;
    background-color: $black;
    color: $white;

    &:hover {
      background-color: $white;
      color: $black;
    }
  }

  .search-form {
    .search-input {
      background-color: $black;
      border: 2px solid $white;
      color: $white;
    }

    .search-button {
      background-color: $black;
      border: 2px solid $white;
      color: $white;

      &:disabled {
        cursor: initial;
        background-color: $black;
        border: 2px solid $white;
        color: $white;
      }

      &:enabled:hover {
        background-color: $white;
        border: 2px solid $black;
        color: $black;
      }
    }
  }

  .filter-current {
    border: 2px solid $white;
    background-color: $white;
    color: $black;

    &:hover {
      background-color: $black;
      color: $white;
    }
  }

  .card {
    border: 2px solid $white;

    .badges {
      .badge {
        background-color: $black;
        color: $white;
        border: 2px solid $white;
      }
    }
  }

  .pagination {
    .active {
      border: 2px solid $black;
      background-color: $white;
      color: $black;
    }

    button {
      border: 2px solid $white;
      background-color: $black;
      color: $white;

      &:enabled:hover {
        border: 2px solid $black;
        background-color: $white;
        color: $black;
      }
    }
  }
}

.mainLayout {
  min-height: 100vh;

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    transition: all 0.3s ease 0s;

    .weather {

    }

    .user {
      display: flex;
      flex-direction: column;
      align-items: center;

      .name {
        user-select: none;
      }
    }
  }

  .moviesListPage {
    padding: 10px;

    .down {
      cursor: pointer;
      position: fixed;
      z-index: 100;
      bottom: 20px;
      right: 20px;
      transition: all 0.3s ease 0s;

      &:hover {
        transform: scale(1.3);
      }
    }

    .filters {
      display: flex;
      column-gap: 10px;
      align-items: center;
      padding: 5px;
      @media (max-width: 1870px) {
        flex-wrap: wrap;
        row-gap: 10px;
      }

      .filter {
        border: 2px solid $black;
        user-select: none;
        transition: all 0.2s ease 0s;
        font-size: 16px;
        font-weight: 700;
        flex: 1 1 200px;
        padding: 5px;
        border-radius: 40px;
        text-align: center;

        &:hover {
          cursor: pointer;
        }
      }

      .filter-current {
        transition: all 0.2s ease 0s;
        font-size: 16px;
        font-weight: 700;
        flex: 1 1 200px;
        padding: 5px;
        border-radius: 40px;
        text-align: center;

        &:hover {
          cursor: pointer;
        }
      }
    }

    .search-form {
      display: flex;
      column-gap: 10px;
      justify-content: flex-end;
      height: 30px;
      padding: 10px;

      .search-input {
        transition: all 0.3s ease 0s;
        font-size: 16px;
        font-weight: 700;
        width: 300px;
        padding: 10px;
        border-radius: 20px;
      }

      .search-button {
        transition: all 0.3s ease 0s;
        font-size: 16px;
        width: 100px;
        cursor: pointer;
        border-radius: 20px;

        &:enabled {
          transform: scale(1.1);
        }
      }
    }

    .cards {
      .loading {
        position: absolute;
      }

      min-height: 100vh;
      padding: 10px;
      display: grid;
      justify-items: center;
      grid-template-columns: repeat(3, 1fr);
      row-gap: 40px;
      @media (max-width: 1620px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 1200px) {
        grid-template-columns: repeat(1, 1fr);
      }

      .card {
        border: 2px solid $black;
        border-radius: 30px;
        display: grid;
        align-content: space-between;
        flex-direction: column;
        width: 500px;
        position: relative;
        overflow: hidden;
        transition: all 0.3s ease 0s;

        @media (max-width: 650px) {
          width: auto;
        }

        @media (min-width: 1200px) {
          &:hover {
            transform: scale(1.05);
          }
        }

        .badges {
          position: absolute;
          top: 10px;
          right: 10px;
          display: flex;
          flex-direction: column;
          align-items: center;
          row-gap: 5px;
          justify-content: center;

          .badge {
            z-index: 10;
            transition: all 0.3s ease 0s;
            font-weight: 650;
            padding: 5px 10px;
            border-radius: 20px;
          }
        }

        .poster {
          cursor: pointer;
          height: 750px;
          position: relative;
          @media (max-width: 700px) {
            height: 0;
            padding-bottom: 142%;
          }

          & img {
            position: absolute;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;
            object-fit: cover;
          }
        }

        .poster-error {
          background-color: grey;
          height: 750px;
        }

        .poster-loading {
          height: 750px;
        }

        .info {
          padding: 10px;
          display: flex;
          flex-direction: column;
          row-gap: 10px;

          a {
            padding: 15px 15px 15px 0;
            color: inherit;
            text-decoration: none;

            &:hover {
              text-decoration: underline;
            }
          }
        }

        .rating {
          padding: 10px;
        }
      }
    }

    .pagination {
      & > * {
        display: flex;
        column-gap: 10px;
      }

      padding: 50px;
      display: flex;
      justify-content: center;
      column-gap: 15px;
      @media (max-width: 1200px) {
        flex-wrap: wrap;
        row-gap: 10px;
      }

      .numbers {
        @media (max-width: 1200px) {
          flex-wrap: wrap;
          justify-content: center;
          row-gap: 10px;
        }
      }

      button {
        &:disabled {
          cursor: inherit;
        }

        user-select: none;
        transition: all 0.3s ease 0s;
        border-radius: 10px;
        padding: 10px 20px;
        font-size: 18px;
        font-weight: 600;
        cursor: pointer;
        @media (max-width: 1200px) {
          padding: 5px 10px;
        }
      }
    }
  }

  .movieDetailsPage {
    a {
      color: inherit;
      font-size: 20px;

      &:hover {
        color: red;
      }
    }

    padding: 10px;

    .title {

    }

    .body {
      display: flex;
      column-gap: 20px;
      font-size: 18px;
      @media (max-width: 1250px) {
        flex-wrap: wrap;
        justify-content: flex-start;
        row-gap: 30px;
        .overview {
          align-content: start;
        }
      }
      @media (max-width: 980px) {
        justify-content: center;
      }

      .poster {
        position: relative;
        width: 500px;
        @media (max-width: 600px) {
          padding-bottom: 142%;
          width: 100%;

          img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
      }

      .info {
        > div:nth-child(2n+1) {
          font-weight: 700;
        }

        flex: 0 0 350px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
        align-content: start;
        @media (max-width: 600px) {
          flex: 1 1 350px;
        }
      }

      .overview {
        max-width: 500px;

        h2 {
          margin: 0 0 20px 0;
        }

        div {
          max-width: 600px;
        }
      }
    }
  }
}
